* {
  font-family: 'Indie Flower', cursive !important;
  font-size: 16px;
  font-weight: 200; }

.info {
  font-size: 1.2em; }

.toggle.toggle-morado input:checked + .track {
  background-color: rgba(137, 107, 235, 0.65); }

.toggle-small input:checked + .track .handle {
  background-color: rgba(137, 107, 235, 0.9); }

ion-header-bar {
  background-color: rgba(137, 107, 235, 0.7) !important; }

.ion-content {
  overflow-scroll: false; }

.bar .title, .bar .buttons-left span, .bar .buttons-right span {
  color: #FFF !important; }

.item .item-content.activated {
  background-color: rgba(137, 107, 235, 0.9);
  border-color: rgba(137, 107, 235, 0.7);
  color: #FFF; }

.item-radio input:checked + .radio-content .item-content {
  background: rgba(137, 107, 235, 0.15); }

.ko {
  background-image: url("../img/ko.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 100%;
  animation: bounceInLeft 2s; }

.botonAlarma {
  height: 30px !important;
  min-height: 30px;
  padding-top: 5px; }

.button.button-full {
  margin-top: 0px; }

.botonAlarma .icon:before {
  font-size: 25px;
  margin-top: -14px;
  padding-right: 5px; }

.botonAlarma .ion-flame:before, .botonAlarma .ion-information {
  font-size: 20px !important; }

.iconoDiario {
  display: inline-block; }

.botonAlarmado {
  background-color: rgba(137, 107, 235, 0.45) !important;
  border: 3px solid rgba(137, 107, 235, 0.2) !important;
  box-shadow: 2px 4px 7px #666; }

.botonInfo {
  background-color: rgba(137, 107, 235, 0.2) !important;
  border: 3px solid rgba(137, 107, 235, 0.15) !important; }

.botonAlarmado:hover, .botonInfo:hover {
  box-shadow: 1px 3px 6px #666; }

.botonAlarmado:active, .botonInfo:active {
  box-shadow: 1px 1px 5px #666; }

.boxAlarma {
  position: absolute;
  right: 0px;
  margin-right: 10px;
  margin-top: -60px;
  width: 100px;
  height: 60px;
  padding-top: 16px;
  padding-left: 4px;
  border-radius: 34px;
  background-color: rgba(137, 107, 235, 0.2);
  border: 3px solid rgba(137, 107, 235, 0.15);
  box-shadow: 2px 4px 7px #666; }
  .boxAlarma .iconoDiario {
    font-size: 40px !important;
    display: inline-block; }

.boxAlarma:hover {
  box-shadow: 1px 3px 6px #666; }

.boxAlarma:active {
  box-shadow: 1px 2px 16px #666; }

.alertaActivada {
  padding-top: 10px;
  padding-left: 4px;
  background-color: rgba(137, 107, 235, 0.65);
  border: 3px solid rgba(137, 107, 235, 0.45); }

.fila {
  height: 47px; }

.sumario {
  background-color: white;
  z-index: 1; }

.row {
  padding: 2px; }

.col {
  padding: 1px; }

.row .scroll-page .icon:before {
  margin-top: 0px !important;
  padding-right: 0px !important; }

.col .icon:before {
  font-size: 60px; }

.col .ion-ios-cloud:before {
  font-size: 46px !important; }

.scroll-page .row + .row {
  margin-top: 0;
  padding: 0px; }

.scroll-page .row:nth-of-type(odd) {
  background: rgba(137, 107, 235, 0.15); }

.scroll-page .row:nth-of-type(even) {
  background: rgba(137, 107, 235, 0.02); }

.scroll-page .wind-icon {
  display: inline-block; }

.scroll-page.center {
  text-align: center; }

.scroll-page .has-header {
  position: relative; }

.scroll-page .primary {
  margin: 0;
  font-size: 4em;
  font-weight: lighter; }

.scroll-page .secondary {
  margin: 0;
  font-size: 12em;
  font-weight: lighter; }

.porcentajeIconoDiario {
  float: left;
  padding-left: 35px;
  top: -40px; }

.barraModal {
  font-size: 12px !important;
  line-height: 20px !important;
  margin: 0px !important; }

.modal {
  max-width: 800px;
  margin: 0 auto; }

.bar {
  border-top: 0px !important;
  border-bottom: 0px !important; }

.row.titulo .col {
  font-weight: 900; }

.itemArreglo {
  top: 42px !important;
  padding: 0px; }

.superLunaRow {
  background-color: rgba(255, 28, 4, 0.21) !important; }

.superLunaCol {
  font-weight: bold;
  color: rgba(255, 4, 4, 0.8); }

.loading-sign .spinner svg {
  width: 90px;
  height: 90px;
  stroke: #444;
  fill: #444;
  display: block;
  margin: auto; }

.loading-wrapper {
  position: absolute;
  background: rgba(255, 255, 255, 0.9); }

.loading-sign {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 50%;
  height: 50%;
  margin-top: 35%; }

.cg-busy-default-text {
  margin: 0 auto;
  font-size: 16px;
  color: #555;
  text-align: center;
  max-width: 400px;
  min-width: auto; }

.ocultar {
  color: #FFF; }

.sinScrollBar {
  overflow: hidden !important; }

.margenIzq {
  margin-left: 10px; }

.action-sheet-group.action-sheet-options button {
  text-align: center !important; }

.overflow-scroll {
  overflow-y: hidden; }

.menu .overflow-scroll {
  overflow-y: scroll !important; }

.config.overflow-scroll {
  overflow-y: scroll !important; }

.item-divider {
  background-color: rgba(137, 107, 235, 0.45); }

.datosClima .item {
  text-align: center;
  padding: 3px; }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes bounceInLeft {
  from, 90%, to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-3000px, 0, 0);
    transform: translate3d(-3000px, 0, 0); }
  90% {
    opacity: 1;
    -webkit-transform: translate3d(5px, 0, 0);
    transform: translate3d(5px, 0, 0); }
  to {
    -webkit-transform: none;
    transform: none; } }

@keyframes bounceOutRight {
  20% {
    opacity: 1;
    -webkit-transform: translate3d(-20px, 0, 0);
    transform: translate3d(-20px, 0, 0); }
  to {
    opacity: 0;
    -webkit-transform: translate3d(2000px, 0, 0);
    transform: translate3d(2000px, 0, 0); } }
